.main-nav {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__toggle {
  position: absolute;
  width: 25px;
  height: 20px;
  background-color: #ffffff;
  border: none;
  right: 20px;
  top: -43px;
  padding: 0;
}

.main-nav__toggle--close {
  background-image: url("../../img/close-menu_mobile.svg");
  background-repeat: no-repeat;
  background-position: right center;
}

.main-nav__item {
  padding: 22px 0 24px;
  border-top: 1px solid #e6e6e6;
  text-align: center;
  cursor: pointer;
}

.main-nav__link {
  list-style: none;
