  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    width: auto;
  }

  .main-nav__list {
    display: flex;
    flex-direction: row;
    gap: 33px;
  }

  .main-nav__item {
    border-top: none;
    padding: 0;
  }

  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
  }

